html, body {
    font-family: 'Roboto', sans-serif;
    padding: 0;
    margin: 0;
  }
  
  #root {
    height: 100vh;
  }
  
  * {
    box-sizing: border-box;
}

.joinOuterContainer {
    display: flex;
    justify-content: center;
    text-align: center;
    height: 100vh;
    align-items: center;
    background-color: rgb(198, 183, 170);;
}

.joinInnerContainer {
    width: 20%;
}

.joinInput {
    border-radius: 5px;
    padding: 15px 20px;
    width: 100%;
    border:2px solid rgb(90, 67, 67);
}

.heading {
    color: white;
    font-size: 2.5em;
    padding-bottom: 10px;
    border-bottom: 2px solid white;
}

.button {
    color: #fff !important;
    text-transform: uppercase;
    background: #2979FF;
    padding: 13px;
    border-radius: 5px;
    display: inline-block;
    border: none;
    width: 100%;
    font-size: 1em;    
}

.mt-20 {
    margin-top: 20px;
}

    @media (min-width: 320px) and (max-width: 480px) {
.joinOuterContainer {
    height: 100%;
}

.joinInnerContainer {
    width: 90%;
}


}

button:focus {
    outline: 0;
    font-weight: bolder;
}